import React from 'react'

import Layout from '../components/Layout'
import Posts from '../components/Posts'
import styled from 'styled-components'

const TagIntroDiv = styled.div`
  margin: 0 auto;
  border-bottom: 1px solid #ebebeb;
  margin-bottom: calc(1.5rem - 1px);
  :first-child {
    margin-top: 1%;
  }
  width: 99%;
  @media (min-width: 767px) {
    width: 90%
  }
`

export default ({ data, pageContext }) => {
  const { posts, tagName } = pageContext

  let pageTitle = tagName !== 'cv' ? `Posts about ${tagName}` : `Programming Portfolio`;

  return (
    <Layout>
      <TagIntroDiv>
        <h1>{pageTitle}</h1>
        {tagName === 'cv' &&
          <p>
            More detailed work related projects as outlined in my résumé.
          <br/><br/>
            For password protected content access, please reach out to me at <a href={`mailto:patmcmullen118@gmail.com`}>patmcmullen118@gmail.com</a>
          </p>
        }
      </TagIntroDiv>
      <Posts data={posts} tagPageName={tagName} />
    </Layout>
  )
}
